<template>
  <div class="container">
    <!-- 头部导航栏 -->
    <navbar>
      <h4 slot="title">我的收藏</h4>
    </navbar>
    <!-- 新闻列表组件 -->
    <listsitem :news="this.collects" />
  </div>
</template>

<script>
import navbar from "@/components/navbar";
import listsitem from "@/components/lists-item";
export default {
  data() {
    return {
      collects: []
    };
  },
  mounted() {
    const info = JSON.parse(localStorage.getItem("user_info"));
    this.$axios({
      url: `/user_star`,
      method: "get",
      // 添加验证token
      headers: {
        Authorization: info.token
      }
    }).then(res => {
      this.collects = res.data.data;
      console.log(this.collects);
    });
  },
  components: {
    navbar,
    listsitem
  }
};
</script>

